import Button from 'components/atoms/Button';
import Gap from 'components/atoms/Gap/Gap';

export default function ModalVideoProfile({
  embedVideo,
  onClose,
}: {
  embedVideo: string;
  onClose: () => void;
}) {
  return (
    <div className="fixed inset-0 bg-primary/70 p-4">
      <Gap className="h-[80px]" />
      <div className="relative aspect-video w-full">
        <div className="absolute top-0 left-0 z-10 aspect-video w-full animate-pulse rounded-lg bg-gray-300" />
        <iframe
          className="relative z-20 aspect-video w-full rounded-lg"
          src={embedVideo}
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        />
      </div>
      <div className="mt-2 flex justify-center">
        <Button onClick={onClose} padding="px-2 py-1" display="inline-block" color="warning">
          <i className="bi bi-close" />
          <span> Close</span>
        </Button>
      </div>
    </div>
  );
}
