import { ICompanyDetail } from '../../interfaces';
import {
  entreExpoDataLine1,
  entreExpoDataLine2,
  entreExpoDataLine3,
  entreExpoDataLine4,
} from '../entre-expo';
import {
  booth1x1companiesLine1,
  booth1x1companiesLine2,
  boothInternshipLine1,
  boothInternshipLine2,
  boothScholarshipLine1,
  boothScholarshipLine2,
} from './Booth1x1';
import booth2x1companies from './Booth2x1';

const companies: ICompanyDetail[] = [
  ...booth2x1companies,
  ...booth1x1companiesLine1,
  ...booth1x1companiesLine2,
  ...boothScholarshipLine1,
  ...boothScholarshipLine2,
  ...boothInternshipLine1,
  ...boothInternshipLine2,
  ...entreExpoDataLine1,
  ...entreExpoDataLine2,
  ...entreExpoDataLine3,
  ...entreExpoDataLine4,
];

export const careerExpoCompanies = [
  ...booth2x1companies,
  ...booth1x1companiesLine1,
  ...booth1x1companiesLine2,
];
// .filter((company) => company.id !== 'blankCompany');

export const scholarshipExpoCompanies = [...boothScholarshipLine1, ...boothScholarshipLine2].filter(
  (company) => company.id !== 'blankCompany'
);

export const internshipExpoCompanies = [...boothInternshipLine1, ...boothInternshipLine2].filter(
  (company) => company.id !== 'blankCompany'
);

export const entrepreneurshipExpoCompanies = [
  ...entreExpoDataLine1,
  ...entreExpoDataLine2,
  ...entreExpoDataLine3,
  ...entreExpoDataLine4,
].filter((company) => company.id !== 'blankCompany');

export default companies;
