import LinkTo from '../../atoms/LinkTo/LinkTo';
import { headerMenuList } from './Header';

export default function MenuMobile() {
  return (
    <ul className="yellow-gradient fixed right-0 top-16 left-0 block gap-10 rounded-b-xl px-6 pt-4 pb-3 font-primary text-white shadow-xl backdrop-blur-lg transition-all duration-300">
      {headerMenuList.map(({ href, title }) => (
        <li key={href} className="mb-3">
          <LinkTo href={href} className="underline-offset-2 text-jobfairPurple-2 hover:text-white/70 hover:underline">
            {title.toUpperCase()}
          </LinkTo>
        </li>
      ))}
    </ul>
  );
}
