import { isMobile } from 'react-device-detect';
import LinkTo from '../../atoms/LinkTo/LinkTo';

export default function LogoGroup() {
  return (
    <div className={`flex ${isMobile ? 'gap-1' : 'gap-3'}`}>
      <LinkTo href="/">
        <img
          src="/img/logos/logo-ipb.png"
          alt="logo ipb"
          className={isMobile ? 'h-10' : 'h-14'}
        />
      </LinkTo>
      <LinkTo href="/">
        <img
          src="/img/logos/logo-cda.png"
          alt="logo cda"
          className={isMobile ? 'h-10' : 'h-14'}
        />
      </LinkTo>
    </div>
  );
}
