import { ReactNode } from 'react';

interface Props {
  children?: ReactNode;
  className?: string;
  display: 'block' | 'inline-block' | 'flex' | 'inline-flex' | 'inline';
  color?: 'primary' | 'warning' | string;
  onClick?: () => void;
  padding?: string;
  disabled?: boolean;
}

export default function Button(props: Props) {
  const { display, padding, children, className, color, onClick, disabled } = props;
  const paddingSize = padding || 'p-3 px-4';
  let buttonColor = '';
  if (color === 'primary') {
    buttonColor = 'yellow-gradient';
  }
  if (color === 'secondary') {
    buttonColor = 'blue-gradient';
  }
  return (
    <button
      type="button"
      disabled={disabled}
      onClick={onClick}
      className={`${className || ''} ${display || 'block'}
     rounded-lg ${buttonColor} ${paddingSize} hover:shadow-lg disabled:cursor-not-allowed disabled:bg-gray-300 disabled:text-gray-600
    `}
    >
      {children}
    </button>
  );
}
