import { lazy, Suspense } from 'react';
import { ToastContainer } from 'react-toastify';
import ReactGA from 'react-ga';
import { Routes, Route, useLocation } from 'react-router-dom';
import TransitionLayer from 'components/atoms/TransitionLayer/TransitionLayer';
import Booth from 'components/pages/Booth/Booth';

const EmployerPage = lazy(() => import('components/pages/EmployerPage/EmployerPage'));
const Lobby = lazy(() => import('components/pages/Lobby/Lobby'));
const IVFAcademyPage = lazy(() => import('components/pages/IVFAcademyPage/IVFAcademyPage'));
const LandingPage = lazy(() => import('components/pages/LandingPage/LandingPage'));
const NotFound = lazy(() => import('components/pages/NotFound/NotFound'));
const VacancyPage = lazy(() => import('components/pages/VacancyPage/VacancyPage'));
const ExpoHall = lazy(() => import('components/pages/ExpoHall/ExpoHall'));
const WebinarSeriesPage = lazy(() => import('components/pages/WebinarSeriesPage/WebinarSeriesPage'));
const EntrepreneurshipExpo = lazy(() => import('components/pages/EntrepreneurshipExpo/EntrepreneurshipExpo'));

// Mobile Version Only
const CareerExpo = lazy(() => import('components/pages/CareerExpo/CareerExpo'));
const InternshipExpo = lazy(() => import('components/pages/InternshipExpo/InternshipExpo'));
const EntrepreneurshipExpoMobile = lazy(() => import('components/pages/EntrepreneurshipExpoMobile/EntrepreneurshipExpoMobile'));
const ScholarshipExpo = lazy(() => import('components/pages/ScholarshipExpo/ScholarshipExpo'));

ReactGA.initialize('UA-49614634-4');

export default function App() {
  const location = useLocation();

  return (
    <>
      <ToastContainer />
      <Suspense fallback={<TransitionLayer showTransition={false} />}>
        <Routes key={location.pathname} location={location}>
          <Route path="/" element={<LandingPage />} />
          <Route path="/employer" element={<EmployerPage />} />
          <Route path="/ijf-academy" element={<IVFAcademyPage />} />
          <Route path="/webinar-series" element={<WebinarSeriesPage />} />
          <Route path="/vacancy" element={<VacancyPage />} />
          <Route path="/expo-hall" element={<Lobby />} />
          <Route path="/expo-area" element={<ExpoHall />} />
          <Route
            path="/entrepreneurship-expo"
            element={<EntrepreneurshipExpo />}
          />
          <Route path="/booth/:companyId" element={<Booth />} />

          {/* Mobile version */}
          <Route path="/career-expo" element={<CareerExpo />} />
          <Route path="/scholarship-expo" element={<ScholarshipExpo />} />
          <Route path="/internship-expo" element={<InternshipExpo />} />
          <Route path="/entrepreneurship-expo/mobile" element={<EntrepreneurshipExpoMobile />} />

          <Route path="*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </>
  );
}
