import { ReactNode } from 'react';
import LinkTo from './LinkTo/LinkTo';

interface Props {
  children?: ReactNode;
  className?: string;
  display: 'block' | 'inline-block' | 'flex' | 'inline-flex' | 'inline';
  href: string;
  color?: 'primary' | 'warning' | string;
  isNativeAnchor?: boolean;
  target?: '_blank' | '_self' | '_parent' | '_top';
  disabled?: boolean;
}

export default function ButtonLink(props: Props) {
  const { href, display, disabled, children, className, color, isNativeAnchor, target } = props;
  let buttonColor = '';
  if (color === 'primary') {
    buttonColor = 'yellow-gradient';
  }
  if (color === 'secondary' || color === 'warning') {
    buttonColor = 'blue-gradient text-white';
  }
  if (isNativeAnchor) {
    return (
      <a
        onClick={(e) => {
          if (disabled) {
            e.preventDefault();
          }
        }}
        target={!target ? '_blank' : target}
        rel="noopener noreferrer"
        href={href}
        className={`${className || ''} ${display || 'block'}
          rounded-lg ${buttonColor} p-3 px-4 duration-150 hover:-translate-y-px hover:-translate-x-px
        hover:shadow-2xl 
          `}
      >
        {children}
      </a>
    );
  }
  return (
    <LinkTo
      href={href}
      className={`${className || ''} ${display || 'block'}
     rounded-lg ${buttonColor} p-3 px-4 duration-150 hover:-translate-y-px hover:-translate-x-px
   hover:shadow-2xl 
     `}
    >
      {children}
    </LinkTo>
  );
}
