import { isTablet, isMobile } from 'react-device-detect';
import { motion } from 'framer-motion';
import MenuDekstop from './MenuDekstop';
import MenuMobile from './MenuMobile';

interface Props {
  showMenu?: boolean;
  active?: string;
}

export default function MenuList(props: Props) {
  const { showMenu, active } = props;
  return (
    <>
      <MenuDekstop active={active} />
      {showMenu && (
        <>
          <div className="fixed top-[64px] left-0 h-screen w-full bg-white/70 backdrop-blur-lg" />
          <motion.div
            variants={{
              initial: { opacity: 0.7 },
              animate: { opacity: 1 },
              exit: { opacity: 0.5 },
            }}
            initial="initial"
            animate="animate"
            exit="exit"
            transition={{ duration: 0.1 }}
          >
            <MenuMobile />
          </motion.div>
        </>
      )}
    </>
  );
}
