import { useReducer } from 'react';
import Header from 'components/molecules/Header/Header';
import { ICompanyDetail } from 'interfaces';
import { Helmet } from 'react-helmet';
import { titlePageGenerator } from 'utils';
import LinkTo from 'components/atoms/LinkTo/LinkTo';
import Button from 'components/atoms/Button';
import IVFWebinarCard from 'components/molecules/IVFWebinarCard/IVFWebinarCard';
import ModalVideoProfile from './ModalVideoProfile';

type TAction = {
  type: 'VIDEO_TOGGLE' | 'PROFILE_TOGGLE';
};

type TInitialState = {
  toggleVideo: boolean;
  toggleProfile: boolean;
};

const initialState: TInitialState = {
  toggleProfile: false,
  toggleVideo: false,
};

const reducer = (state: TInitialState, action: TAction) => {
  if (action.type === 'PROFILE_TOGGLE') {
    return { ...state, toggleProfile: !state.toggleProfile };
  }
  if (action.type === 'VIDEO_TOGGLE') {
    return { ...state, toggleVideo: !state.toggleVideo };
  }
  return state;
};

export default function BoothMobile({ company }: { company: ICompanyDetail }) {
  const { vacancies, embedVideo, webinar, address, image, name, profile } = company;

  const [state, dispatch] = useReducer(reducer, initialState);

  return (
    <>
      <Helmet>
        <title>{titlePageGenerator(name)}</title>
      </Helmet>
      <div className="screenWrapper pb-5 font-primary">
        <Header />
        <div className="flex justify-between p-4">
          <Button
            padding="px-2 py-1"
            onClick={() => window.history.back()}
            display="inline-block"
            className="text-white"
            color="primary"
          >
            Back
          </Button>
          {embedVideo && (
            <>
              <Button
                onClick={() => dispatch({ type: 'VIDEO_TOGGLE' })}
                padding="px-2 py-1"
                display="inline-block"
                color="warning"
              >
                <i className="bi bi-play-circle" />
                <span> Video Profile</span>
              </Button>
              {state.toggleVideo && (
                <ModalVideoProfile
                  onClose={() => dispatch({ type: 'VIDEO_TOGGLE' })}
                  embedVideo={embedVideo}
                />
              )}
            </>
          )}
        </div>
        <div className="flex items-start gap-3 px-4">
          <div className="w-[120px] flex-shrink-0">
            <img
              src={image}
              alt={name}
              className="aspect-square w-[120px] rounded-lg border border-primary"
            />
          </div>
          <div>
            <h2 className="text-lg font-bold text-primary">{name}</h2>
            <div className="mt-1 flex gap-1">
              <i className="bi bi-geo-fill" />
              <p>{address}</p>
            </div>
          </div>
        </div>
        <div className="mb-2 px-4">
          {state.toggleProfile ? (
            <>
              <p dangerouslySetInnerHTML={{ __html: profile }} />
              <button
                onClick={() => dispatch({ type: 'PROFILE_TOGGLE' })}
                className="text-blue-500 underline underline-offset-2"
              >
                Hide
              </button>
            </>
          ) : (
            <>
              <p dangerouslySetInnerHTML={{ __html: `${profile.slice(0, 200)}...` }} />
              <button
                onClick={() => dispatch({ type: 'PROFILE_TOGGLE' })}
                className="text-blue-500 underline underline-offset-2"
              >
                Show More
              </button>
            </>
          )}
        </div>
        {vacancies && (
          <div className="mb-2 p-4">
            <h2 className="mb-2 text-center text-lg font-bold text-primary">Vacancy</h2>
            {vacancies.map((vacancy) => (
              <div className="mb-2 flex w-full items-center justify-between border-b pb-1">
                <div>
                  <p>{vacancy.title}</p>
                </div>
                {!vacancy.closed ? (
                  <a href={vacancy.link} target="_blank" rel="noreferrer" className="block">
                    <Button padding="py-1 px-2" className="text-sm" color="warning" display="block">
                      Lamar
                    </Button>
                  </a>
                ) : (
                  <Button padding="py-1 px-2" disabled className="text-sm" color="warning" display="block">
                    Tutup
                  </Button>
                )}
              </div>
            ))}
          </div>
        )}
        {webinar && (
          <div className="p-4">
            <h2 className="mb-2 text-center text-lg font-bold text-primary">Webinar</h2>
            {webinar.map((item) => (
              <IVFWebinarCard webinarCard={item} ctaColor="primary" />
            ))}
          </div>
        )}
      </div>
    </>
  );
}
